.header {
  background: #e2e2e2;
  font-size: 26px;
}

.header__inner {
  display: flex;
  flex: 1 1;
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  padding: 20px;
  margin: 0 auto;
}

.layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.layout__content {
  display: flex;
  flex: 1 1;
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}

.layout__inner {
  position: relative;
  width: 100%;
}

.panel {
    padding: 20px;
    background: #e2e2e2;
}
.page {
  display: flex;
  flex-direction: column;
}

.page__main {
  display: flex;
}

.page__sidebar {
  width: 300px;
}

.page__content {
  width: 100%;
  min-height: 2000px;
  background: #bada55;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

